<template>
    <div class="container">
        <h1 class="my-4">App météo avec vue JS</h1>

        <div class="form-groupe mb-5">

            <label for="position">Entrez le nom d'une ville</label>
            <input 
            id="position" 
            type="text"
            class="form-control"
            v-model="requete"
            v-on:keypress="goMeteo"
            >
        </div>
        <div v-if="temps" class="w-75 m-auto">
            <h3 class="text-center mb-3">Position: {{temps.name}}</h3>

            <div class="card text-center p-5">
                <p class="text-affichage">
                    Temperature : {{temps.main.temp.toFixed()}} <!-- toFixed() arrondit la température-->
                </p>
                <p class="text-affichage">
                    Temp : {{temps.weather[0].description}}
                </p>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
    export default {
        name:'Meteo',
        data(){
            return{
                requete: '', /*ce qu'on recherche dans l'input*/
                temps: undefined, 
                api_code: "e61bff373ba7073122b8cd7ec7ecf5ed", /*cle de l API*/
                url_recherche: 'https://api.openweathermap.org/data/2.5/weather?'
            }
        },
        methods: {
            goMeteo(e){
                if(e.key == "Enter"){  /* Si on appuie sur la touche enter */
                    axios
                    .get(`${this.url_recherche}q=${this.requete}&units=metric&APPID=${this.api_code}&lang=fr`)
                    .then(reponse => {
                        //console.log(reponse);
                        this.temps = reponse.data;
                        //console.log(this.temps);

                    })
                    this.requete=""
                }
            }
        }
    }
</script>    



<style>

.text-affichage {
    font-size: 30px;
    font-weight: 300;
    line-height: 1.2;
}
</style>